.card {
  position: relative;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 4rem;
  border-radius: 7px;
  line-height: 1;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.17);
}

.card-image {
  position: absolute;
  border-radius: 100%;
  width: 6rem;
  height: 6rem;
  border: 1px solid black;
  top: -3rem;
  left: 30%;
}
.card-body {
  margin-top: 3rem;
}
.card-title {
  font-size: 20px;
  font-weight: bold;
  color: #2145b7;
}

.txt {
  font-weight: bold;
}
/*desktops*/
@media only screen and (min-width: 1200px) {
}
/*mobile*/
@media screen and (max-width: 480px) {
}
/*tablet*/
@media only screen and (min-width: 481px) and (max-width: 800px) {
  .card-title {
    font-size: 10px;
  }
}
/*bigger tablets*/
@media (min-width: 800px) and (max-width: 1200px) {
  .card-title {
    font-size: 13px;
  }
}
